
h1{
	font-size: 100px;
}
.titleLanding{
	color: #004047;
	font-size: 80px;
	text-align: center;
	position:absolute; 
	left: 50%;  
	margin-left: -400px; 
	top: 50%; 
	height: 30px; 
	margin-top: -100px; 
}

.startBtn{
	font-size: 40px;
	background-color: #fff;
	border: 2px solid #004047;
	border-radius: 5px;
	width: 200px;
	height: 100px;
}


@media only screen and (max-width: 600px) {
	h1{
		font-size: 35px;
	}
	.titleLanding{
		position: relative;
		left: 0;
		margin-left: 0px;
		margin-top: 0px;
	}
	.startBtn{
		font-size: 30px;
		background-color: #fff;
		border: 2px solid #004047;
		border-radius: 5px;
		width: 150px;
		height: 80px;
	}
} 